@import url("http://fonts.googleapis.com/css2?family=Sora&display=swap");

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
  /* font-family: Barlow; */
  color: #ffffff;
}

/* Main body */
body {
