            linear-gradient(127deg, rgba(0,255,0,.8), rgba(0,255,0,0) 70.71%),
            linear-gradient(336deg, rgba(0,0,255,.8), rgba(0,0,255,0) 70.71%); */
  background-image: url(../src/Images/ÃœberUns/Desktop//Fotos/textureImage.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.bodyComponentLayout {
  margin-inline: 5.5%;
}
/* Navigation css*/
.MuiAppBar-positionStatic.MuiAppBar-positionStatic {
  background-color: transparent;
}

h1 {
  font-size: 4.9vw;
}
h2 {
  font-size: 4.25vw;
  font-family: TGL0-1451Engschrift;
}
p {
  font-size: 0.7vw;
  font-family: Barlow;
}
/* .h5 {
  font-family: TGL0-1451Engschrift !important;
  font-size: 20px;
  text-align: left;
  margin-bottom: 0px;
} */

h6 {
  color: darkgoldenrod;

  margin: 0px;
}
h3 {
  /* color: darkgoldenrod;  */
  font-size: 2vw;
  color: #ecbd46;
  font-family: TGL0-1451Engschrift;
}
h4 {
  font-size: 1.2vw;
  color: #ecbd46;
}

/* */

/* Button css*/

.MuiButton-root.MuiButton-root {
  font-weight: 500;
  font-stretch: semi-condensed;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
}
.MuiTypography-body2.MuiTypography-body2 {
  color: "#a4a4a4";
}
/* footer+/ css*/
.makeStyles-footer-8.makeStyles-footer-8 {
  border-top: 1px solid #ffffff;
}

/* used for the height of text field for message in contact us page */
/* .MuiOutlinedInput-root.MuiOutlinedInput-root {
  height: 262px;
} */

.MuiTypography-colorTextSecondary.MuiTypography-colorTextSecondary {
  color: #ffffff;
}
.MuiPaper-root.MuiPaper-root {
  background-color: black;
}
.IpadPro,
.mobile,
.IpadQuer {
  display: none;
}
.dash {
  width: 14%;
  margin-top: -40px;
}
.image {
  max-height: 350px;
}

/* media querry*/
.textHeaderColor {
  color: rgb 236 189 70;
}

@media (max-width: 1366px) {
  .desktop,
  .mobile,
  .IpadQuer {
    display: none;
  }
  .IpadPro {
    display: block;
  }
  p {
    font-size: 1.2vw;
    font-family: Barlow;
  }
}
@media (max-width: 1024px) {
  .desktop,
  .mobile,
  .IpadPro {
    display: none;
  }
  .IpadQuer {
    display: block;
  }
  p {
    font-size: 1.2vw;
    font-family: Barlow;
  }
}
@media (max-width: 375px) {
  .desktop,
  .IpadPro,
  .IpadQuer {
    display: none;
    font-size: 2px;
  }
  .mobile {
    display: block;
  }
  h1 {
    font-size: 2vw;
  }
  h2 {
    font-size: 1.78vw;
  }
  p {
    font-size: 1.2vw;
    font-family: Barlow;
  }
}



